import { Component } from '@angular/core';
import { CountdownService} from './countdown.service';
import { AiService } from 'app/ai.service';
import { timeout } from 'q';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'Countdown Game';
  countdown : number = 1; 
  playerTurn : number ;
  timeout : number = 600 ; 
  isGameEnded : boolean = false ; 
  disableButtons : boolean ; 


  constructor(private countdownService : CountdownService , private aiService : AiService){
    this.countdown = countdownService.getCountdown() ; 
    this.playerTurn = 1 ; 
    this.disableButtons = false ; 
  }

  decrementCountdown(amount : number) : void {
    this.countdown = this.countdownService.decrementCountdown(amount);
  }

  playerPlayed(amount : number) : void {
    this.disableButtons = true ; 
    var i = 0 ;
    for(var i = 0 ; i < amount ; i++){
      setTimeout(() => {
        this.decrementCountdown(1)
      }, this.timeout*(i) );  
    }

    setTimeout(() => {
       this.changePlayerTurn()
    }, this.timeout*(i-1) ); 

    
  }

  changePlayerTurn(){
    if(this.countdown == 1){
      this.isGameEnded = true ; 
    }
    else if(this.playerTurn == 1){
      this.playerTurn = 2 ; 
      setTimeout(() => {
        this.playerPlayed(this.aiService.makeMove(this.countdown))
      }, this.timeout*2 ); 
      
    }
    else {
      this.disableButtons = false ; 
      this.playerTurn = 1 ; 
    }
  }


}



