import { Injectable } from '@angular/core';

@Injectable()
export class CountdownService {
  private countdown : number ; 
  constructor() { 
     this.countdown = 20 ; 
  }

  setCountdown(amount : number) : number{
    this.countdown = amount ;  
    return this.countdown ; 
  }

  decrementCountdown(amount : number) : number{
    this.countdown -= amount; 
    if(this.countdown <= 1){
      this.countdown = 1 ; 
    }
    
    return this.countdown ; 
  }

  isCountdownOne() : boolean {
    if(this.countdown == 1){
      return true ; 
    }
    else {
      return false ; 
    }
  }

  getCountdown() : number {
    return this.countdown ; 
  }



}



